import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
//http://185.253.154.185/api
//http://localhost:3100/api
export default new Vuex.Store({
   state: {
      apiUrl: "http://185.253.154.185/api",
      user: null,
   },
   mutations: {
      GET_USER_DATA(state, userData) {
         state.user = userData;
      },
   },
   actions: {
      getUserDate({ commit }) {
         let data = localStorage.getItem("hrLogin");
         commit("GET_USER_DATA", JSON.parse(data));
      },
   },
   modules: {},
});
