<template>
   <div>
      <div>
         <div>
            <v-img
               :src="require('../assets/mammon.png')"
               class="my-3"
               contain
               height="200"
            />
            <h3 style="text-align: center; padding: 10px">
               كلية المأمون الجامعة
            </h3>
         </div>
      </div>
      <v-row align="center" justify="space-around">
         <v-col cols="4"> </v-col>
         <v-col cols="4" align="center">
            <v-text-field
               solo
               dark
               v-model="userName"
               label="اسم المستخدم"
            ></v-text-field>
            <v-text-field
               solo
               dark
               v-model="password"
               type="password"
               label="كلمة المرور"
            ></v-text-field>

            <v-btn color="primary" @click="login">
               <v-icon left> mdi-login-variant </v-icon>
               تسجيل دخول
            </v-btn>
         </v-col>
         <v-col cols="4"> </v-col>
      </v-row>

      <v-snackbar v-model="snackbar" :timeout="timeout">
         {{ text }}
         <template v-slot:action="{ attrs }">
            <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>
   </div>
</template>

<script>
export default {
   data: () => ({
      password: "",
      userName: "",
      text: "",
      snackbar: false,
      timeout: 2000,
   }),
   methods: {
      login() {
         let self = this;
         if (self.userName && self.password) {
            let credentials = {
               userName: self.userName,
               password: self.password,
            };
            self.$http
               .post(`${self.$store.state.apiUrl}/login`, credentials)
               .then((res) => {
                  console.log(res.data);
                  localStorage.setItem("hrLogin", JSON.stringify(res.data));
                  location.reload();
               })
               .catch((err) => {
                  console.log(err);
                  self.snackbar = true;
                  self.text = "الرجاء ادخال معلومات صحيحة";
               });
         } else {
            self.snackbar = true;
            self.text = "الرجاء ادخال  اسم المستخدم وكلمة المرور";
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
