<template>
   <div>
      <v-container class="container" fluid>
         <v-card dark>
            <v-row style="margin: 10px">
               <v-col cols="10">
                  <h3>صفحة الكتب الواردة</h3>
               </v-col>

               <v-col cols="2">
                  <v-tooltip bottom>
                     <template v-slot:activator="{ on, attrs }">
                        <v-btn
                           class="mx-2"
                           fab
                           dark
                           small
                           color="stepper"
                           @click="searchDialog = true"
                           v-bind="attrs"
                           v-on="on"
                        >
                           <v-icon dark> mdi-magnify </v-icon>
                        </v-btn>
                     </template>
                     <span>بحث</span>
                  </v-tooltip>

                  <v-tooltip bottom>
                     <template v-slot:activator="{ on, attrs }">
                        <v-btn
                           v-if="$store.state.user.roleId != 3"
                           class="mx-2"
                           fab
                           dark
                           small
                           color="stepper"
                           @click="dialog = true"
                           v-bind="attrs"
                           v-on="on"
                        >
                           <v-icon dark> mdi-plus </v-icon>
                        </v-btn>
                     </template>
                     <span>اضافة</span>
                  </v-tooltip>
               </v-col>
            </v-row>
         </v-card>
      </v-container>

      <v-container class="container" fluid>
         <v-data-table
            dark
            :headers="headers"
            :items="inner"
            :items-per-page="10"
            class="table-content"
         >
            <template v-slot:item.index="{ item, index }">
               <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
            </template>
            <template
               v-slot:item.actions="{ item }"
               v-if="$store.state.user.roleId != 3"
            >
               <!-- <v-btn icon dark @click="dialog = false">
                  <v-icon @click="editItem(item)"> mdi-pencil </v-icon>
               </v-btn> -->

               <v-btn icon fab dark small @click="dialog = false">
                  <v-icon @click="deleteItem(item)"> mdi-delete </v-icon>
               </v-btn>
            </template>
         </v-data-table>
      </v-container>

      <v-dialog
         v-model="dialog"
         dark
         persistent
         max-width="600px"
         width="600px"
      >
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon dark @click="dialog = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>اضافة كتاب الواردة</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>
               <v-row>
                  <v-col cols="6">
                     <v-text-field
                        v-if="$store.state.user.sectionId != 2"
                        solo
                        v-model="from"
                        label="من"
                     ></v-text-field>
                     <v-text-field
                        v-if="$store.state.user.sectionId == 2"
                        solo
                        v-model="from"
                        label="جهة الاصدار"
                     ></v-text-field>
                     <v-autocomplete
                        v-model="archiveSubject"
                        :items="items"
                        item-text="subjectName"
                        item-value="idArchiveSubject"
                        solo
                        filled
                        label="الموضوع"
                     ></v-autocomplete>
                     <v-dialog
                        ref="dialog"
                        v-model="datePickerAddModel"
                        persistent
                        width="290px"
                     >
                        <template v-slot:activator="{ on, attrs }">
                           <v-text-field
                              v-if="$store.state.user.sectionId != 2"
                              v-model="innerDate"
                              label="اختر التاريخ"
                              readonly
                              solo
                              v-bind="attrs"
                              v-on="on"
                           ></v-text-field>
                           <v-text-field
                              v-if="$store.state.user.sectionId == 2"
                              v-model="innerDate"
                              label="تاريخ صادرة الكتاب الوارد"
                              readonly
                              solo
                              v-bind="attrs"
                              v-on="on"
                           ></v-text-field>
                        </template>
                        <v-date-picker v-model="addedDate" scrollable>
                           <v-spacer></v-spacer>
                           <v-btn
                              text
                              color="primary"
                              @click="datePickerAddModel = false"
                           >
                              الغاء
                           </v-btn>
                           <v-btn text color="primary" @click="getDates">
                              موافق
                           </v-btn>
                        </v-date-picker>
                     </v-dialog>
                  </v-col>
                  <v-col cols="6">
                     <v-text-field
                        solo
                        v-model="to"
                        label="الى"
                        v-if="$store.state.user.sectionId != 2"
                     ></v-text-field>

                     <v-text-field
                        solo
                        v-model="archiveNumber"
                        label="رقم صادرة الكتاب الوارد"
                        v-if="$store.state.user.sectionId == 2"
                     ></v-text-field>

                     <v-text-field
                        solo
                        v-model="archiveNumber"
                        label="العدد"
                        v-if="$store.state.user.sectionId != 2"
                     ></v-text-field>
                     <v-file-input
                        multiple
                        label="الملفات"
                        solo
                        dense
                        v-model="files"
                        @change="getAllFiles"
                     ></v-file-input>
                  </v-col>
               </v-row>

               <v-textarea
                  solo
                  rows="3"
                  row-height="15"
                  name="input-7-4"
                  v-model="archiveDescription"
                  label="نص الكتاب الواردة"
               ></v-textarea>

               <v-textarea
                  v-if="$store.state.user.sectionId != 2"
                  rows="3"
                  row-height="15"
                  solo
                  v-model="note"
                  name="input-7-4"
                  label="الملاحظات"
               ></v-textarea>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn color="secondary" text @click="saveInnerData">
                  حفظ
               </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>

      <v-dialog
         dark
         v-model="searchDialog"
         persistent
         max-width="600px"
         width="600px"
      >
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon dark @click="searchDialog = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>بحث في الواردة</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>
               <v-autocomplete
                  v-model="subjectSearch"
                  :items="items"
                  item-text="subjectName"
                  item-value="idArchiveSubject"
                  solo
                  dense
                  filled
                  label="الموضوع"
               ></v-autocomplete>

               <v-row>
                  <v-col cols="12" sm="6">
                     <v-dialog
                        ref="dialog"
                        v-model="datePickerSearchModel"
                        persistent
                        width="290px"
                     >
                        <template v-slot:activator="{ on, attrs }">
                           <v-text-field
                              v-model="dateRangeText"
                              label="اختر التاريخ من - الى"
                              readonly
                              solo
                              v-bind="attrs"
                              v-on="on"
                           ></v-text-field>
                        </template>
                        <v-date-picker v-model="dates" scrollable range>
                           <v-spacer></v-spacer>
                           <v-btn
                              text
                              color="primary"
                              @click="datePickerSearchModel = false"
                           >
                              الغاء
                           </v-btn>
                           <v-btn text color="primary" @click="getDates">
                              موافق
                           </v-btn>
                        </v-date-picker>
                     </v-dialog>
                  </v-col>
                  <v-col cols="12" sm="6">
                     <v-text-field
                        solo
                        label="العدد"
                        v-model="numberSearch"
                     ></v-text-field>
                  </v-col>
               </v-row>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn color="secondary" text @click="searchData"> بحث </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>

      <v-snackbar v-model="snackbar" :timeout="timeout">
         {{ text }}
         <template v-slot:action="{ attrs }">
            <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>

      <v-dialog
         dark
         v-model="deleteDialog"
         persistent
         max-width="600px"
         width="600px"
      >
         <v-card>
            <v-toolbar color="error">
               <v-btn icon dark @click="deleteDialog = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>تأكيد حذف</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>
               <h3 style="color: #ff6b6b">هل انت متأكد من حذف البيانات؟</h3>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn color="error" text @click="confirmDelete"> موافق </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
   </div>
</template>

<script>
import moment from "moment";
export default {
   data() {
      return {
         items: [],

         datePickerSearchModel: false,
         files: [],

         dialog: false,
         datePickerAddModel: false,
         addedDate: "",
         deleteDialog: false,
         itemToDelete: "",

         searchDialog: false,
         archiveSubject: "",
         archiveDescription: "",
         note: "",
         from: "",
         to: "",
         archiveNumber: "",

         subjectSearch: "",
         numberSearch: "",
         dates: [],

         snackbar: false,
         text: "My timeout is set to 2000.",
         timeout: 2000,

         headers: [
            { text: "العدد", value: "archiveNumber" },
            { text: "التاريخ", value: "archiveDate" },
            { text: "من", value: "from" },
            { text: "الى", value: "to" },
            { text: "ملاحظات", value: "note" },
            {
               text: "الموضوع",
               align: "start",
               sortable: false,
               value: "archiveSubject",
            },
            { text: "الاجراءات", value: "actions" },
         ],
         inner: [],
      };
   },
   created() {
      let self = this;
      if (self.$store.state.user.sectionId == 2) {
         console.log(self.$store.state.user);

         self.headers = [
            { text: "رقم الوارد", value: "index" },
            { text: "تاريخ الوارد", value: "createdAt" },
            { text: "جهة الاصدار", value: "from" },
            { text: "رقم صادرة الكتاب الوارد", value: "archiveNumber" },
            { text: "تاريخ صادرة الكتاب الوارد", value: "archiveDate" },
            {
               text: "الموضوع",
               align: "start",
               value: "archiveSubject",
            },
            {
               text: "نص الكتاب",
               value: "subjectDescription",
            },
            { text: "الاجراءات", value: "actions" },
         ];
      }
      self.initialData();
   },
   computed: {
      dateRangeText() {
         return this.dates.length > 0
            ? this.dates.join(" - ")
            : "تاريخ الكتاب من - الى";
      },
      innerDate() {
         return this.addedDate;
      },
   },
   methods: {
      initialData() {
         let self = this;
         self.$http
            .get(`${self.$store.state.apiUrl}/searchArchive?archiveTypeId=2`)
            .then((res) => {
               console.log(res.data);
               self.inner = res.data.map((inner) => {
                  return {
                     archiveDate: moment(inner.archiveDate).format(
                        "DD/MM/YYYY"
                     ),
                     archiveNumber: inner.archiveNumber,
                     archiveSubject: inner.archiveSubject.subjectName,
                     from: inner.from,
                     idArchive: inner.idArchive,
                     note: inner.note,
                     sectionId: inner.sectionId,
                     subjectDescription: inner.subjectDescription,
                     to: inner.to,
                     createdAt: moment(inner.createdAt).format("DD/MM/YYYY"),
                  };
               });
            });
         self.$http
            .get(`${self.$store.state.apiUrl}/archiveSubjects`)
            .then((res) => {
               console.log(res.data);
               self.items = res.data;
            });
      },
      getAllFiles() {
         console.log(this.files);
      },
      searchData() {
         let self = this;
         let query = "";

         if (self.dates.length > 0) {
            query += `&dates=${JSON.stringify(self.dates)}`;
         }

         if (self.numberSearch) {
            query += `&numberSearch=${self.numberSearch}`;
         }

         if (self.subjectSearch) {
            query += `&subjectSearch=${self.subjectSearch}`;
         }

         self.$http
            .get(`${self.$store.state.apiUrl}/searchArchive?1=1${query}`)
            .then((res) => {
               self.inner = res.data.map((inner) => {
                  return {
                     archiveDate: moment(inner.archiveDate).format(
                        "DD/MM/YYYY"
                     ),
                     archiveNumber: inner.archiveNumber,
                     archiveSubject: inner.archiveSubject.subjectName,
                     from: inner.from,
                     idArchive: inner.idArchive,
                     note: inner.note,
                     sectionId: inner.sectionId,
                     subjectDescription: inner.subjectDescription,
                     to: inner.to,
                     createdAt: moment(inner.createdAt).format("DD/MM/YYYY"),
                  };
               });
               console.log(res.data);
            });

         this.searchDialog = false;
      },
      getDates() {
         this.datePickerSearchModel = false;
         this.datePickerAddModel = false;
      },
      confirmDelete() {
         let self = this;
         self.$http
            .delete(
               `${self.$store.state.apiUrl}/archive/${self.itemToDelete.idArchive}`
            )
            .then((res) => {
               self.snackbar = true;
               self.text = "تم حذف الكتاب ";
               self.deleteDialog = false;
               self.initialData();
               console.log(res.data);
            });
      },
      deleteItem(item) {
         let self = this;
         self.deleteDialog = true;
         self.itemToDelete = item;
      },
      saveInnerData() {
         let self = this;

         if (
            self.$store.state.user.sectionId != 2 &&
            self.addedDate &&
            self.archiveSubject &&
            self.note &&
            self.archiveDescription &&
            self.from &&
            self.to &&
            self.archiveNumber
         ) {
            let innerData = {
               from: self.from,
               to: self.to,
               archiveDate: self.addedDate,
               archiveSubjectId: self.archiveSubject,
               archiveNumber: self.archiveNumber,
               subjectDescription: self.archiveDescription,
               note: self.note,
               sectionId: self.$store.state.user.sectionId,
               archiveTypeId: 2,
            };
            self.$http
               .post(`${self.$store.state.apiUrl}/addArchive`, innerData)
               .then((res) => {
                  self.snackbar = true;
                  self.text = "تم اضافة البيانات";
                  self.initialData();
                  console.log(res);
               });
         } else if (
            self.$store.state.user.sectionId == 2 &&
            self.addedDate &&
            self.archiveSubject &&
            self.from &&
            self.archiveDescription &&
            self.archiveNumber
         ) {
            console.log("done");
            let innerData = {
               from: self.from,
               to: "الادارية",
               archiveDate: self.addedDate,
               archiveSubjectId: self.archiveSubject,
               archiveNumber: self.archiveNumber,
               subjectDescription: self.archiveDescription,
               note: "لايوجد",
               sectionId: self.$store.state.user.sectionId,
               archiveTypeId: 2,
            };
            self.$http
               .post(`${self.$store.state.apiUrl}/addArchive`, innerData)
               .then((res) => {
                  self.snackbar = true;
                  self.text = "تم اضافة البيانات";
                  self.initialData();
                  console.log(res);
               });
         } else {
            self.snackbar = true;
            self.text = "الرجاء ملئ كافة الحقول";
         }

         this.dialog = false;
      },
      parseDate(date) {
         if (!date) return null;
         const [month, day, year] = date.split("/");
         return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
      },
   },
};
</script>

<style lang="scss" scoped>
.theme--dark.v-card {
   background-color: #363636;
}
.theme--dark.v-data-table {
   background-color: #363636;
}
</style>
