<template>
   <div>
      <v-container class="container" fluid>
         <v-card dark>
            <v-row style="margin: 10px">
               <v-col cols="10">
                  <h3>صفحة المواضيع</h3>
               </v-col>
            </v-row>
         </v-card>
      </v-container>
      <v-row>
         <v-col cols="6">
            <v-container class="container" fluid>
               <v-card dark>
                  <v-card-title>
                     المواضيع
                     <v-spacer></v-spacer>
                     <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="بحث"
                        single-line
                        hide-details
                        solo
                     ></v-text-field>
                  </v-card-title>
                  <v-data-table
                     dark
                     :headers="headers"
                     :items="subjectFilter"
                     :items-per-page="5"
                     class="table-content"
                  >
                     <template
                        v-slot:item.actions="{ item }"
                        v-if="$store.state.user.roleId != 3"
                     >
                        <v-btn icon dark @click="dialog = false">
                           <v-icon @click="editItem(item)"> mdi-pencil </v-icon>
                        </v-btn>

                        <v-btn icon fab dark small @click="dialog = false">
                           <v-icon @click="deleteItem(item)">
                              mdi-delete
                           </v-icon>
                        </v-btn>
                     </template>
                  </v-data-table>
               </v-card>
            </v-container>
         </v-col>
         <v-col cols="6">
            <v-card dark class="add-container">
               <v-text-field
                  solo
                  v-model="subjectName"
                  label="اسم الموضوع"
                  dark
               ></v-text-field>
               <v-btn
                  color="secondary"
                  text
                  @click="saveInnerData"
                  v-if="$store.state.user.roleId != 3"
               >
                  حفظ
               </v-btn>
            </v-card>
         </v-col>
      </v-row>

      <v-dialog
         dark
         v-model="editDialog"
         persistent
         max-width="600px"
         width="600px"
      >
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon dark @click="editDialog = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>تعديل الموضوع</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>
               <v-row>
                  <v-col cols="12" sm="6">
                     <v-text-field
                        solo
                        label="اسم الموضوع"
                        v-model="editSubjectDetails.subjectName"
                     ></v-text-field>
                  </v-col>
               </v-row>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn color="secondary" text @click="editSubject">
                  تعديل
               </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>

      <v-snackbar v-model="snackbar" :timeout="timeout">
         {{ text }}
         <template v-slot:action="{ attrs }">
            <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
               اغلاق
            </v-btn>
         </template>
      </v-snackbar>

      <v-dialog
         dark
         v-model="deleteDialog"
         persistent
         max-width="600px"
         width="600px"
      >
         <v-card>
            <v-toolbar color="error">
               <v-btn icon dark @click="deleteDialog = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>تأكيد حذف</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>
               <h3 style="color: #ff6b6b">هل انت متأكد من حذف البيانات؟</h3>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn color="error" text @click="confirmDelete"> موافق </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
   </div>
</template>

<script>
export default {
   data() {
      return {
         editDialog: false,
         search: "",
         itemToDelete: "",

         subjectName: "",
         editSubjectDetails: "",

         deleteDialog: false,
         snackbar: false,
         text: "My timeout is set to 2000.",
         timeout: 2000,

         headers: [
            { text: "العدد", value: "idArchiveSubject" },
            { text: "الموضوع", value: "subjectName" },
            { text: "الاجراءات", value: "actions" },
         ],
         subjects: [],
      };
   },
   created() {
      let self = this;
      self.initialData();
   },
   computed: {
      subjectFilter() {
         if (this.search) {
            return this.subjects.filter((subject) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) => subject.subjectName.toLowerCase().includes(v));
            });
         } else {
            return this.subjects;
         }
      },
   },
   methods: {
      initialData() {
         let self = this;
         self.$http
            .get(`${self.$store.state.apiUrl}/archiveSubjects`)
            .then((res) => {
               console.log(res.data);
               self.subjects = res.data;
            });
      },
      getAllFiles() {
         console.log(this.files);
      },
      confirmDelete() {
         let self = this;
         self.$http
            .delete(
               `${self.$store.state.apiUrl}/archiveSubject/${self.itemToDelete.idArchiveSubject}`
            )
            .then((res) => {
               self.snackbar = true;
               self.text = "تم حذف الموضوع ";
               self.deleteDialog = false;
               self.initialData();
               console.log(res.data);
            });
      },
      deleteItem(item) {
         let self = this;
         self.deleteDialog = true;
         self.itemToDelete = item;
      },
      editItem(subject) {
         console.log(subject);
         this.editSubjectDetails = subject;
         this.editDialog = true;
      },
      editSubject() {
         let self = this;
         self.$http
            .put(
               `${self.$store.state.apiUrl}/archiveSubject/${self.editSubjectDetails.idArchiveSubject}`,
               self.editSubjectDetails
            )
            .then((res) => {
               self.snackbar = true;
               self.text = "تم تعديل البيانات";
               self.initialData();
               self.editDialog = false;
               console.log(res);
            })
            .catch((err) => {
               self.snackbar = false;
               self.text = "حدث خطأ في الرجاء التأكد من البيانات";
               self.editDialog = true;
            });
      },
      saveInnerData() {
         let self = this;

         if (self.subjectName) {
            let data = { subjectName: self.subjectName };
            self.$http
               .post(`${self.$store.state.apiUrl}/addArchiveSubject`, data)
               .then((res) => {
                  self.snackbar = true;
                  self.text = "تم اضافة البيانات";
                  self.initialData();
                  console.log(res);
                  self.subjectName = "";
               })
               .catch((err) => {
                  self.snackbar = true;
                  self.text = "حدث خطأ في الرجاء التأكد من البيانات";
               });
         } else {
            self.snackbar = true;
            self.text = "الرجاء ملئ كافة الحقول";
         }
      },
   },
};
</script>

