<template>
   <v-app>
      <v-app-bar app color="primary" dark v-if="login">
         <div class="d-flex align-center">
            <v-app-bar-nav-icon @click.stop="drawer = !drawer">
            </v-app-bar-nav-icon>
         </div>

         <v-spacer></v-spacer>

         <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
               <h3 v-bind="attrs" v-on="on">برنامج الارشفة الالكترونية</h3>
            </template>
            <span>الاصدار : V 1.0.0</span>
         </v-tooltip>
      </v-app-bar>
      <v-navigation-drawer v-model="drawer" absolute temporary dark right>
         <v-list-item>
            <v-list-item-avatar>
               <v-img
                  :src="require('./assets/mammon.png')"
                  class="my-3"
                  contain
                  height="200"
               />
            </v-list-item-avatar>

            <v-list-item-content>
               <v-list-item-title>كلية المأمون</v-list-item-title>
            </v-list-item-content>
         </v-list-item>

         <v-divider></v-divider>

         <v-list dense>
            <v-list-item v-for="item in items" :key="item.title" link>
               <v-list-item-icon>
                  <v-icon>{{ item.icon }}</v-icon>
               </v-list-item-icon>

               <v-list-item-content @click="page(item)">
                  <v-list-item-title>{{ item.name }}</v-list-item-title>
               </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item style="margin-top: 20px">
               <div style="margin-top: 20px"></div>
               <v-btn @click="logOut" class="not-printable">
                  <v-icon icon> mdi-logout-variant</v-icon>
                  تسجيل خروج
               </v-btn>
            </v-list-item>
         </v-list>
      </v-navigation-drawer>
      <v-main>
         <router-view />
      </v-main>
   </v-app>
</template>

<script>
export default {
   name: "App",

   data: () => ({
      drawer: null,
      login: false,
      pageTitle: "الصفحة الرئيسية",
      items: [
         { title: "Home", icon: "mdi-home", name: "الصفحة الرئيسية" },
         { title: "Inner", icon: "mdi-file-download", name: "صفحة الوارد" },
         { title: "Outer", icon: "mdi-file-upload", name: "صفحة الصادر" },
         { title: "Subjects", icon: "mdi-book-outline", name: "صفحة المواضيع" },
      ],
   }),
   created() {
      this.$store
         .dispatch("getUserDate")
         .then(() => {
            if (this.$store.state.user) {
               this.login = true;
               this.$router.push("/home").catch(() => {});
            } else {
               this.$router.push("/login").catch(() => {});
               this.login = false;
            }
         })
         .catch(() => {});
      console.log(this.$store.state.user);
   },
   methods: {
      page(item) {
         this.$router.push(item.title).catch(() => {});
      },
      logOut() {
         localStorage.removeItem("hrLogin");
         location.reload();
      },
   },
};
</script>
<style>
main {
   background: rgb(126, 120, 120);
}
</style>
